<template>
  <section class="timer" >
    <div class="timer-heading" data-aos="fade-up">
      <h2>Our Webapp is Almost Ready</h2>
    </div>
    <div class="date" data-aos="fade-up">
      <div class="date-items">
        <p class="date-items-main">{{displayDays}}</p>
        <p class="date-items-sub">Days</p> 
      </div>
      <span></span>
      <div class="date-items">
        <p class="date-items-main">{{displayHours}}</p>
        <p class="date-items-sub">Hrs</p>
      </div>  
      <span></span>
      <div class="date-items">
        <p class="date-items-main">{{displayMinutes}}</p>
        <p class="date-items-sub">Mins</p>
      </div>
      <span></span>
      <div class="date-items">
        <p class="date-items-main">{{displaySeconds}}</p>
        <p class="date-items-sub">Secs</p>
      </div>
    </div>
  </section>
</template>

<script>
export default {
 data () {
   return {
     displayDays: 0,
     displayHours: 0,
     displayMinutes: 0,
     displaySeconds: 0
   }
 },
 computed: {
   seconds : () => 1000,
   minutes () {
     return this.seconds * 60;
   },
   hours () {
     return this.minutes * 60;
   },
   days () {
     return this.hours * 24;
   }
 },
 mounted () {
   this.showRemaining()
 },
 methods: {
  formatNum (num) {
    return num < 10 ? '0' + num : num;
  },
  showRemaining () {
    const timer = setInterval(() => {
        const now = new Date()
        const end = new Date(2021, 1, 11, 10, 10,10,10);
        const distance = end.getTime() - now.getTime();

        if(distance < 0) {
          clearInterval(timer);
          return
        }
        const days = Math.floor(distance / this.days);
        const hours = Math.floor((distance % this.days) / this.hours);
        const minutes = Math.floor((distance % this.hours) / this.minutes);
        const seconds = Math.floor((distance % this.minutes) / this.seconds);
        this.displayMinutes = this.formatNum(minutes);
        this.displaySeconds = this.formatNum(seconds);
        this.displayHours = this.formatNum(hours);
        this.displayDays = this.formatNum(days)
      }, 1000);
    }
  }
}
</script>

<style lang="scss" scoped>
 @import "../assets/scss/index.scss";
 .timer{
   background:linear-gradient(#0505057c, rgba(15, 1, 1, 0.4),transparent), url('~@/assets/images/Timer.png');
  //  background-size: cover;
  //  height: 652px;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-content: center;
   text-align: center;
  position: relative;
    height: 500px;
    overflow: hidden;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
   &-heading{
    color: $white;
    margin-bottom: 3rem;
    h2 {
      font-family: 'Inter', sans-serif;
      font-style: normal;
      font-weight: $regular;
      font-size: 36px;
      line-height: 48px;
      text-transform: capitalize;
    }
  }
  .date {
    font-family: 'Inter', sans-serif;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
    text-align: center;
    color:  $white;
    &-items {
      margin-right: 20px;
      text-transform: uppercase;
      &-main {
        font-weight: 500;
        font-size: 36px;
        line-height: 48px;
        
      }
      &-sub {
        font-weight: 500;
        font-size: 18px;
        line-height: 48px;
        margin-top: -1rem;
      }
    }
  }
  span {
    border: 1px solid $white;
    height: 3rem;
    margin-right: 22px;
  }
 }

 //Mobile Styles
  @media screen and (max-width: 600px) {
    .timer{
      &-heading{
        margin-bottom: 1rem;
        h2 {
          font-size: 25px;
        }
      }
    }
    .date {
      &-items {
        margin-right: 27px;
        text-transform: uppercase;
        &-main {
          font-weight: 500;
          font-size: 22px !important;
          line-height: 48px;
        }
        &-sub {
          font-weight: 500;
          font-size: 16px;
          line-height: 48px;
          margin-top: 1rem;
        }
      }
    } 
  }
   @media screen and (max-width: 400px) {
    .timer{
      &-heading{
        margin-bottom: 1rem;
        h2 {
          font-size: 20px;
        }
      }
    }
    .date {
      &-items {
        margin-right: 1px !important;
        text-transform: uppercase;
        &-main {
          font-weight: $medium !important;
          font-size: 20px !important;
          line-height: 48px;
        }
        &-sub {
          font-weight: $medium !important;
          font-size: 18px !important;
          line-height: 48px;
          margin-top: 1rem;
        }
      }
    } 
    span {
      margin-left: 4px;
    }
  }
</style>